.App{
  margin: 40px auto;
  width: 40%;
  min-height: 500px;
  background-color: #07031A;
  border-radius: 10px;
  box-shadow: 0 3px 20px 1px #07031A;
}

@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap');

body{
  background-color: #4f8a8b;
  font-family: 'Josefin Sans', sans-serif;
}

.main{
  background-color: #f4f6ff;
  border: none;
  border-radius: 10px;
  color: #07031A;
  width: 60%;
  height: 40px;
  font-size: 20px;
  font-family: 'Josefin Sans', sans-serif;
  margin-left: 5%;
  padding: 10px;
  margin-top:5%;
  margin-bottom: 20px;
}

input::placeholder{
  color: #4f8a8b;
  opacity: 0.5;
}

input:focus{
  outline:none;
}

.add{
  width: 20%;
  margin-left: 5%;
  margin-right: 5%;
  height: 50px;
  background-color: #fbd46d;
  border: 2px solid #fbd46d;
  border-radius: 10px;
  padding: 10px;
  font-size: 20px;
  font-family: 'Josefin Sans', sans-serif;
  cursor: pointer;
  color: #07031A;
  
}

button:focus{
  outline: none;
}

button:hover{
  color: #fbd46d;
  background-color: #07031A;
  transition: 0.4s ease-in-out;
  border: 2px solid #fbd46d;
}

.list{
  color:#f4f6ff;
  width: 80%;
  background-color: #4f8a8b;
  margin-left: 7.5%;
  height: auto;
  border-radius: 5px;
  margin-top: 20px;
  margin-right: 10%;
  font-size: 18px;
  text-align: justify;
  padding-left:15px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right: 15px;
}

.date{
  font-size: 12px;
}

.delete{
  width: 30px;
  height: 30px;
  font-size: 14px;
  text-align: center;
  padding: none;
  border: 2px solid #fbd46d;
  background-color: #fbd46d;
  border-radius: 10px;
  cursor: pointer;
  float: right;
  position: relative;
  top: -50px;
}

.list p input{
  background-color: transparent;
  height: 30px;
  border: none;
  font-size: 20px;
  font-family: 'Josefin Sans', sans-serif;
}
.list p input:focus{
  outline:none;
}